import React, { useMemo } from "react";
import { Segment, Header } from "semantic-ui-react";
import { groupBy } from "lodash";
import SKILLS from "./skills.json";

const SKILLS_BY_RARITY = groupBy(SKILLS, "rarity");
const RARITY_P_BY_SLOT = [
  { common: 0.833, rare: 0.125, epic: 0.042 },
  { common: 0.752, rare: 0.188, epic: 0.06 },
  { common: 0.714, rare: 0.214, epic: 0.071 },
  { common: 0.7, rare: 0.2, epic: 0.1 }, // guess
];

const getExpectedWinRate = (currentSkills, possibilities) => {
  let totalValue = 0;
  const curr = { common: 0, rare: 0, epic: 0 };
  currentSkills.forEach((s) => {
    curr[SKILLS[s].rarity] += 1;
  });
  possibilities.forEach(([skills, winRate]) => {
    const newSkills = skills.filter((s) => !currentSkills.includes(s));
    const probability = newSkills.reduce((acc, skill, i) => {
      const slotRarity = SKILLS[skill].rarity;
      const slotIdx = currentSkills.length - 1 + i;
      const slotProbability =
        RARITY_P_BY_SLOT[slotIdx][slotRarity] /
        (SKILLS_BY_RARITY[slotRarity].length - curr[slotRarity]);
      return acc * slotProbability;
    }, 1);
    totalValue += probability * winRate;
  });
  return totalValue;
};

const ExpectedWinRate = ({ currentSkills, possibilities }) => {
  const eWin = useMemo(() => getExpectedWinRate(currentSkills, possibilities), [
    currentSkills,
    possibilities,
  ]);

  return (
    <Segment>
      <Header size="huge">{`${Math.floor(eWin)}%`}</Header>
    </Segment>
  );
};

export default ExpectedWinRate;
