import React from "react";
import { Form } from "semantic-ui-react";

const EitherOr = ({ name, val, setVal, opt1, opt2 }) => (
  <Form.Field>
    <label>{name}</label>
    <Form.Radio
      label={opt1}
      name={name}
      value={opt1}
      checked={val === opt1}
      onChange={() => setVal(opt1)}
    />
    <Form.Radio
      label={opt2}
      name={name}
      value={opt2}
      checked={val === opt2}
      onChange={() => setVal(opt2)}
    />
  </Form.Field>
);

export default EitherOr;
