import React, { useMemo } from "react";
import { every } from "lodash";
import { Grid, Header, Image, Label, Segment } from "semantic-ui-react";

import DATA from "./data/data.json";
import images from "./images";
import ExpectedWinRate from "./ExpectedWinRate";

const getColor = (winRate) => {
  if (winRate > 90) return "green";
  if (winRate > 80) return "orange";
  return "red";
};

const getPossibilities = (setSkills, enchant, level) => {
  const config = DATA[level][enchant];
  return Object.keys(config)
    .map((set) => [set.split("-"), config[set]])
    .sort((a, b) => b[1] - a[1])
    .filter(([set]) => every(setSkills.map((s) => set.includes(s))));
};

const SkillSuggestor = ({ skills, enchant, level }) => {
  const avail = useMemo(() => getPossibilities(skills, enchant, level), [
    skills,
    enchant,
    level,
  ]);
  return (
    <Grid.Column width={skills.length === 2 ? 8 : 4}>
      <Header size="medium">Expected win rate without rerolling</Header>
      <ExpectedWinRate currentSkills={skills} possibilities={avail} />
      <Header size="medium">Best possibilities</Header>
      <Segment>
        {avail.slice(0, 10).map((t) => {
          return (
            <Suggestion key={t[0].join("")} skills={skills} suggested={t} />
          );
        })}
      </Segment>
    </Grid.Column>
  );
};

const Suggestion = ({ skills, suggested }) => {
  const newToMe = suggested[0].filter((s) => !skills.includes(s));

  return (
    <Grid.Row>
      {newToMe.map((s) => (
        <Image key={s} src={images(s)} inline spaced size="tiny" />
      ))}
      <Label color={getColor(suggested[1])}>{`${Math.floor(
        suggested[1]
      )}%`}</Label>
    </Grid.Row>
  );
};

export default SkillSuggestor;
