import React, { useCallback, useState } from "react";
import { Form, Grid, Image, Header, Segment } from "semantic-ui-react";
import images from "./images";
import EitherOr from "./EitherOr";
import SkillSelector from "./SkillSelector";
import SkillSuggestor from "./SkillSuggestor";

const SKILL_LEVELS = [5, 10, 23, 35];

const Skill = ({ idx, skill, setSkill }) => {
  return (
    <Grid.Column computer={4} mobile={8}>
      <Header size="medium">{`Level ${SKILL_LEVELS[idx]} skill`}</Header>
      <Segment>
        <SkillSelector
          setSkill={(e, { value }) => setSkill(idx, value)}
          skill={skill}
        />
        {skill && <Image src={images(skill)} />}
      </Segment>
    </Grid.Column>
  );
};

const DancerForm = () => {
  const [enchant, setEnchant] = useState("lion");
  const [level, setLevel] = useState(33);
  const [skills, setSkills] = useState([null, null, null, null]);

  const skillCountForLevel = level < 35 ? 2 : 3;
  const showSkills = skills.slice(0, skillCountForLevel);

  const setSkill = useCallback(
    (idx, newSkill) =>
      setSkills((oldSkills) =>
        oldSkills.map((s, i) => (i === idx ? newSkill : s))
      ),
    []
  );

  return (
    <Form>
      <Grid columns={3}>
        <Grid.Column>
          <Segment>
            <EitherOr
              name="Enchant"
              val={enchant}
              setVal={setEnchant}
              opt1="lion"
              opt2="cat"
            />
          </Segment>
        </Grid.Column>
        <Grid.Column>
          <Segment>
            <EitherOr
              name="Level"
              val={level}
              setVal={setLevel}
              opt1={33}
              opt2={35}
            />
          </Segment>
        </Grid.Column>
      </Grid>
      <Grid columns={4}>
        {showSkills.map((skill, idx) => (
          <Skill
            key={`${skill}-${idx}`}
            idx={idx}
            skill={skill}
            setSkill={setSkill}
          />
        ))}
        {!showSkills.filter((s) => s === null).length && (
          <SkillSuggestor skills={showSkills} enchant={enchant} level={level} />
        )}
      </Grid>
    </Form>
  );
};

export default DancerForm;
