const importAll = (r) => {
  let images = {};
  r.keys().forEach((item) => {
    images[item.replace("./", "").replace(".png", "")] = r(item);
  });
  return images;
};

const images = importAll(require.context(".", false, /.png/));
const getImage = (name) => images[name].default;

export const imageNames = Object.keys(images);
export default getImage;
